<template>
    <section id="estoque">
        <div class="card" id="tipos">
            <h2 class="card-header">
                Categorias de produtos 
            </h2>
            <div id="list-tipos">
                <div class="toolbar">
                    <button  class="btn btn-success" @click="$modal.show('addNovaCategoria');">Adicionar Nova Categoria</button>
                </div>
                <hr>
                <div class="card-body" > 
                    <fieldset class="form-group" >
                        <label >Pesquisar</label>
                        <input type="text" placeholder="ex: Bebidas"  v-model="filtros.categoria"> 
                    </fieldset>
                    <table >
                        <thead>
                            <tr>
                                <th>Categoria</th>
                                <th>Tributação(%)</th>
                                <th>Ação</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item) in filteredCategoria.slice(0,filtros.limiteCategoria)" v-bind:key="item.id">
                                <td>  <input type="text" v-model="item.categoria"> </td>
                                <td> <input type="number" v-model="item.perc_tributacao" step="0.01"></td>
                                <td class="acoes">
                                    <button class="btn btn-warning" title="Atualizar"  @click="putCategoria(item)"> <font-awesome-icon icon="sync"  /></button>
                                    <button v-if="item.id != 0 && !item.locked " class="btn btn-danger" title="Excluir" @click="deleteCategoria(item)">X</button>
                                </td>
                            </tr>
                            <tfoot>
                                <tr>
                                    <th>{{categorias.slice(0,filtros.limiteCategoria).length}} de {{categorias.length}}</th>
                                </tr>
                            </tfoot>
                
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div class="card" id="produtos">
            <h2 class="card-header">
                Cadastrar/Gerenciar Estoque
            </h2>
            <div id="list-estoques">
                <div class="toolbar toolbar-produto">
                    <button  class="btn btn-success" @click="$modal.show('addNovoProdutoModal')" >Cadastrar novo produto</button>
                    <span class="smallInfo">{{produtos.length}} Produtos encontrados</span>
                    <form id="pesquisa-produto" v-on:submit.prevent="pesquisarProduto()"> 
                        <label >Pesquisar produto </label>
                        <input type="text" placeholder="ex: pão " v-model="filtros.produto" >
                        <button  title="Pesquisar"  class="btn btn-success"> <font-awesome-icon icon="search"  /></button>
                    </form>

                </div>
            </div>
            <hr>
            <div class="card-body">
                <h1 v-if="produtos.length == 0">Nenhum produto encontrado</h1>
                 <table v-if="produtos.length > 0" >
                    <thead>
                        <tr>
                            <th>Código</th>
                            <th>Produto</th>
                            <th>Preço liquido</th>
                            <th>Disponível</th>
                            <th>Categoria</th>
                            <th>Ação</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="produto in produtos" v-bind:key="produto.id">
                            <td>{{produto.id.toString()}}</td>
                            <td> <input type="text" v-model="produto.descricao" style="width:100%"> </td>
                            <td> <input type="number" step="0.01" v-model="produto.preco"> 
                                <small class="smallInfo">
                                (R$ {{ masked_preco(get_valorLiquido(produto))}}) 
                                </small> 
                            </td>
                           
                            <td> <input type="number" v-model="produto.estoque"> </td>
                            <td>
                                <select v-model="produto.categoria_id">
                                    <option :value="categoria.id" v-for="categoria in categorias" v-bind:key="categoria.id" :selected=" produto.categoria_id == categoria.id ">
                                        {{categoria.categoria}}
                                    </option>
                                </select>
                            </td>
                            <td>
                                 <button class="btn btn-warning" title="Atualizar"  @click="putProduto(produto)" > <font-awesome-icon icon="sync" /></button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>

        </div>

         <!-- Modais -->
        <modal name="addNovaCategoria" height="auto">
            <form class="card-body form" name="novaCat" v-on:submit.prevent="addCategoria(this)">
                    <fieldset class="form-group" >
                        <label >Categoria</label>
                        <input type="text" placeholder="Nome do Tipo/Categoria" name="nome" v-model="novaCategoria.categoria" required>
                    </fieldset>
                    <fieldset class="form-group" >
                        <label >Percentual de tributação</label>
                        <input type="number"  min="0" max="100" name="perc_tributacao" step="0.01"  v-model="novaCategoria.perc_tributacao" required > 
                    </fieldset>
                    <button  class="btn btn-success" type="submit">Adicionar</button>
                    <button  class="btn btn-warning" type="button"  style="margin-left:5px" @click="$modal.hide('addNovaCategoria'), $toasted.show('Cancelado')">Cancelar</button>
            </form>
        </modal>
        <modal name="addNovoProdutoModal" height="auto">
            <form class="card-body form" v-on:submit.prevent="addProduto()" >
                <fieldset class="form-group" >
                    <label >Descrição</label>
                    <input type="text" placeholder="Nome do produto ou descrição" v-model="novoProduto.descricao" required>
                </fieldset>
                <fieldset class="form-group" >
                    <label >Preço (R$)</label>
                    <input type="number" placeholder="Preço sem imposto" step="0.01" v-model="novoProduto.preco" required> 
                </fieldset>
                <fieldset class="form-group" >
                    <label >Estoque Inicial</label>
                    <input type="number" min="0"  v-model="novoProduto.estoque">
                </fieldset>
                <fieldset class="form-group" >
                    <label >Categoria</label>
                    <select name=""  v-model="novoProduto.categoria_id">
                        <option v-for="item in categorias" :value="item.id" v-bind:key="item.id">{{item.categoria}} ({{item.perc_tributacao}}%) </option>
                    </select>
                </fieldset>
                <button  class="btn btn-success" type="submit">Adicionar</button>
                <button  class="btn btn-warning" type="button" style="margin-left:5px" @click="$modal.hide('addNovoProdutoModal'), $toasted.show('Terminado')">Terminar</button>
            </form>
        </modal>

    </section>
</template>

<script>
import Api from '@/services/Api'
export default {
    data(){
        return {
            filtros:{limiteCategoria:10,limiteEstoque:100,categoria:'',produto:''},
            categorias:[
                // {id:0,categoria:'Categoria Padrão',perc_tributacao:20.0,locked:true,padrao:true},
                // {id:1,categoria:'Bebidas',perc_tributacao:35.0},
                // {id:2,categoria:'Frios',perc_tributacao:12.0},
            ],
            produtos:[
                //{id:0,descricao:'Iogurte Light', categoria_id:0,preco:7.99,estoque:22}
            ],
            novaCategoria:{id:null,categoria:'',perc_tributacao:0},
            novoProduto:{id:null,descricao:'', categoria_id:0,preco:0.0,estoque:0}
        }
    },
    methods:{
        async getCategorias(){
            //Obter as categorias
            try {
                 const getCats = await Api().get('/categorias/categorias')
                this.categorias = getCats.data
            } catch (error) {
                alert('erro ao buscar categorias')
            }
       },
       async addCategoria(){
            const post = await Api().post('/categorias/categorias' ,this.novaCategoria)
            if(post.data.status && post.data.status != 'ok'){
                alert('Erro ao inserir categoria',post.data.mensagem)
                return
            }
            this.novaCategoria.id = post.data.id
            this.categorias.push( JSON.parse(JSON.stringify(this.novaCategoria)) ) 
            this.$toasted.show(`Categoria '${this.novaCategoria.categoria}' cadastrada`)
            this.novaCategoria.categoria = ''
            this.novaCategoria.perc_tributacao = 0
            this.$modal.hide('addNovaCategoria');
        },
        async putCategoria(categoria){
            const put = await Api().put('/categorias/categorias' ,categoria)
            if(put.data.status && put.data.status != 'ok'){
                alert('Erro ao alterar categoria',put.data.mensagem)
                return
            }
            this.$toasted.show(`Categoria '${categoria.categoria}' alterada`)

        },
       async deleteCategoria(categoria){
            const del = await Api().delete(`/categorias/categorias?id=${categoria.id}` )
            if(del.data.status && del.data.status != 'ok'){
                alert('Erro ao deletar categoria',del.data.mensagem)
                return
            }
            this.$toasted.show(`Categoria '${categoria.categoria}' excluida`)
            this.categorias = this.categorias.filter(item=>{return item.id != categoria.id})
        },

        // --------- produto --------------

        async addProduto(){
            const post = await Api().post('/produtos/produtos' ,this.novoProduto)
             if(post.data.status && post.data.status != 'ok'){
                alert('Erro ao inserir produto',post.data.mensagem)
                return
            }
            this.novoProduto.id = post.data.id
            this.produtos.push( JSON.parse(JSON.stringify(this.novoProduto)))
            this.novoProduto.descricao = ''
            this.novoProduto.preco = 0
            this.novoProduto.categoria_id = 0
            this.novoProduto.estoque = 0
            this.$toasted.show(`Produto '${this.novoProduto.descricao}' cadastrado`)
            this.$modal.hide('addNovoProduto');
        },
        async pesquisarProduto(){
            const result = await Api().get(`http://ec2-52-15-180-127.us-east-2.compute.amazonaws.com/teste-se/php/produtos/pesquisar?q=${this.filtros.produto}`)
            if(result.data.status && result.data.status != 'ok'){
                alert('Erro ao pesquisar',result.data.mensagem)
                return
            }

            this.produtos = result.data
        },

        async putProduto(produto){
            const put = await Api().put('/produtos/produtos' ,produto)
                if(put.data.status && put.data.status != 'ok'){
                    alert('Erro ao atualizar produto',put.data.mensagem)
                return
            }
            await this.getCategorias()
            this.$toasted.show(`Produto '${produto.descricao}' atualizado`)
        },

        masked_preco:(preco)=>{return preco.toFixed(2).toString().replace('.',',')},

        hide (modal) {
            this.$modal.hide(modal);
        },
 

        findByID(item,id){
            const result = item.filter(item=>{ return item.id == id})
            const retorno = result.lenght > 0 ? result[0] : this.categorias[0]
            return retorno

        },
        get_valorLiquido(item){
            let filtro = this.categorias.filter(cat=>{return cat.id == item.categoria_id})
            const preco = parseFloat(item.preco)
            return (preco + (preco * (filtro[0].perc_tributacao/100)))
        },
    },
    computed:{
        filteredCategoria(){
            return this.categorias.filter(item=>{return  item.categoria.toLowerCase().indexOf(this.filtros.categoria.toLowerCase())>-1;})
        }
    },
    async beforeMount(){
        try {
            this.getCategorias()
            //Obter os produtos
            const getProds = await Api().get('/produtos/produtos')
            this.produtos = getProds.data
        } catch (error) {
            alert('erro ao buscar categorias: ' +error)
        }
    }
}
</script>

<style scoped>
    #estoque            { display: grid; grid-template-areas: "tipos produtos  produtos produtos " ; grid-gap: var(--padding) ; padding: 0 var(--padding); height: 100%; max-height: 100%;}

    #tipos              {grid-area: tipos}
    #produtos           {grid-area: produtos}
    #list-tipos,
    #list-estoques      { display: grid; grid-template-rows:68px 1fr;}
    .acoes > *          { margin: 2px 2px; padding: 5px; width: 40px}



    .toolbar > *            { margin-right: 10px;}
    .toolbar-produto        { justify-content: space-between;}
    #pesquisa-produto  > *  { margin-left: 10px;}

    @media (max-width: 1240px) {
        .smallInfo  {display: none}
        #estoque    { grid-template-areas: "produtos" "tipos" }
        td *        { width: 100%; display: block }
    }

</style>
