<template>
  <div  id="venda">
    <Tabela class="tabela" :lista="lista" v-on:remover="remover"/>
    <h2 class="total">Total a pagar: &nbsp; <strong> &nbsp;R${{ masked_preco( total ) }}   </strong>&nbsp; (R$ {{ masked_preco( imposto )}} de imposto)</h2>
    <div class="coletor">
      <div id="group-coleta">
      <input type="text" class="input-coleta" placeholder="Insira o código do item" v-on:keyup.13="addItem" v-model="coleta">
      <input type="button" class="btn btn-success" value="OK" @click="addItem" >
      </div>
      <input type="button" class="btn btn-warning" value="Finalizar compra" :disabled="(lista.length == 0) || excedido" @click="$modal.show('finalizar');">
    </div>

    <!-- Modais -->
    <modal name="finalizar" height="auto" width="30%">
      <div id="container-cupom">
        <h2>Resumo da compra</h2>
        <div id="cupom">
          <h3>Desafio Soft Expert </h3>
          <p> Data da Emissão: {{new Date().toLocaleString('pt-BR', { timeZone: 'America/Sao_Paulo' })}}</p> 
          <table>
            <thead>
              <th>Item(qtd)</th>
              <th>Preço Unit</th>
              <th>Tributação</th>
              <th>Total</th>
            </thead>
            <tbody >
              <tr v-for="(item,idx) in lista" v-bind:key="idx">
                <td>{{item.descricao}} (x{{item.quantidade.toString().padStart(2, '0')}})   </td>
                <td>R${{masked_preco( (item.preco +(item.preco * (item.perc_tributacao/100))) )}} </td>
                <td>R${{masked_preco( ((item.preco * (item.perc_tributacao/100))) )}}  </td>
                <td>R${{masked_preco( (item.preco +(item.preco * (item.perc_tributacao/100))) * item.quantidade)}}  </td>
              </tr>
            </tbody>
            <tfoot>
                <td colspan="3"> <strong>Total a pagar: R${{masked_preco(total)}}</strong>  (Impostos: R${{masked_preco(imposto)}}) </td>
            </tfoot>
          </table>
        </div>
        <input type="button" class="btn btn-success" value="Concluir compra"  @click="$modal.hide('finalizar'),concluir()" >
      </div>


    </modal>
  </div>
</template>

<script>
import Tabela from '../TabelaItens'
import Api from '@/services/Api'
export default {
  name: 'Venda',
  props: {
    msg: String
  },
  components:{ Tabela},
  data(){
    return{
      coleta : '',
      /**
       * Lista de itens da compra
       */
      lista:[
        // {id:1231,produto:'Cerveja American IPA',quantidade:1,preco:18.00,tributacao:0.35 },
        // {id:1232,produto:'Cerveja Red Ale', quantidade:1,preco:22.95,tributacao:0.35 },
        // {id:1242,produto:'Salgado de Milho ', quantidade:1, preco:3.80, tributacao:0.12 },
      ]
    }
  },
  methods:{
    async addItem(){
      try {
        const getProds = await Api().get('/produtos/produtos?id='+this.coleta)

        if(this.coleta.trim() == "" ){
          return
        }
        this.coleta = ''
        if(getProds.data.length == 0){
           this.$toasted.show(`Produto não encontrado`)
          return
        }

        let produto = getProds.data[0]


        if(produto.estoque == 0){
          this.$toasted.show(`Estoque esgotado para este produto!`)
          return
        }

        let verifica = this.lista.filter(item=>{return item.id == produto.id})
       if(verifica.length > 0) {
          this.lista.forEach((item,idx)=>{
            if((item.id == produto.id) && item.quantidade < item.estoque){
              this.$toasted.show(`Quantidade de ${produto.descricao} Adicionada`)
              this.lista[idx].quantidade++
            }
            else if((item.id == produto.id) && item.quantidade >= item.estoque) {
              alert(`Atingido estoque máximo de ${produto.descricao}! `)
            }
          })
          
          return
       }


        produto.preco = parseFloat(produto.preco)
        produto.perc_tributacao = parseFloat(produto.perc_tributacao)
        produto.quantidade = 1
        this.lista.push(produto)
      } catch (error) {
        alert('Erro ao coletar produto',error)
      }

    },

    masked_preco:(preco)=>{
      return parseFloat(preco).toFixed(2).toString().replace('.',',')
      },
    remover(idx){
      this.lista.splice(idx,1)
    },

    async concluir(){

      let itens = this.lista.map(item=>{ return {id: item.id, quantidade:item.quantidade}})
      try {
        const retorno = await Api().post('compra',itens)
        if(retorno.data.status && retorno.data.status == 'ok'){
          this.$toasted.show(`Compra registrada com sucesso!`)
          this.lista = []
        }
        else{
          alert('houve um erro ao registrar compra',retorno.data.status)
        }
      } catch (error) {
        alert('erro ao processar compra',error)
      }
    }
  },
   computed:{
     /**
      * Total da Nota
      */
      total(){
        if(this.excedido){
          alert('Um dos produtos excede o estoque!')
        }
        const vals = this.lista.map(item=>{return (item.preco +(item.preco * (item.perc_tributacao/100)))  * item.quantidade})

        const acc =  vals.reduce((prev,next)=>{
          return prev+next
        },0)

        return acc
      },
      /**
       * Calcula o toal de imposto
       */
      imposto(){
          const vals = this.lista.map(item=>{return (item.preco * (item.perc_tributacao/100)) * item.quantidade})
          const acc =  vals.reduce((prev,next)=>{
            return prev+next
          },0)
          return acc
      },


      excedido(){
        if (this.lista.length > 0) {
          let excede = this.lista.filter(item =>{ return item.quantidade > item.estoque })
          return excede.length > 0 ? true : false
        }
        return false
      }

  },

}
</script>

