  #venda       { display: grid;  grid-template-rows: 1fr auto auto; min-height: 100%;  }
 .tabela       { grid-area: "tabela"; align-self: baseline; padding-bottom: 60px;} 
 .coletor      { grid-area: "coletor"; background:var(--gradient-primary); height: 60px;  display: flex; align-content: center; 
                 padding: var(--padding); justify-content: space-between;} 
 .total        { color: white; background-color: #1290d3; display: flex; padding: 5px; border-radius: var(--border-radius); }
 .input-coleta { text-align: left; font-size: 20px; color: black; border-radius: var(--border-radius); background: white; min-width: 30%; margin-right: 10px;}
  #group-coleta { min-width: 20%;display: flex; }


  td,th {background: #fffda9; color: black; }

  #container-cupom    { padding: 10px;}
  #container-cupom h2 { text-align: center; margin-bottom: 10px;}
  #cupom              { background: #fffda9;  padding: 5px 10px; margin: 20px 0;}
  #cupom h3           { text-align: center;  margin: 30px 0 10px 0;}
  #cupom p           {   margin: 30px 0 10px 0;}


@media (max-width: 768px) {
  #venda      { grid-template-rows: 1fr  auto;}
  .coletor    { height: 50px; padding: calc(var(--padding)/2);}

}
</style>
