<template>
    <div>
        <table>
            <thead>
                <tr>
                    <th>#</th>
                    <th>Produto</th>
                    <th>Quantidade</th>
                    <th>Preço Unitário</th>
                    <th>Tributação</th>
                    <th>Total</th>
                    <th>Ações</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,idx) in lista" :key="idx">
                    <td>{{idx + 1}}</td>
                    <td><small>({{item.id}})</small> {{item.descricao}} </td>
                    <td> 
                       <input type="number" v-model="item.quantidade" min="1" :max="item.estoque" >
                    </td>
                    <td>R$ {{masked_preco(item.preco +(item.preco * (item.perc_tributacao/100)) )}}</td>
                    <td> {{item.perc_tributacao  }}% (R$ {{ masked_preco(perc_tributado(item.preco,item.perc_tributacao))}} ) </td>
                    <td> <strong>R$ {{masked_preco((item.preco +(item.preco * (item.perc_tributacao/100)) )  *item.quantidade)}}</strong> </td>
                    <td>
                        <button class="btn btn-danger" @click="$emit('remover',idx)">X</button>
                    </td>
                </tr>
            </tbody>
        </table>

    </div>
</template>

<script>
//import Btn from './InputNumber'
export default {
    props:['lista'],
    data(){
        return{
        valor:2 
        }
    },
    components: {
    //Btn
  },
  methods:{
      masked_preco:(preco)=>{return parseFloat(preco).toFixed(2).toString().replace('.',',')},
      perc_tributado:(preco,tributacao)=>{ return preco*(tributacao/100)}
  },

}
</script>

