import Vue from 'vue'
import Router from 'vue-router'
import Vendas from '@/components/paginas/Venda'
import Estoque from '@/components/paginas/Estoque'
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Vendas',
      component: Vendas
    },
    {
      path: '/estoque',
      name: 'Estoque',
      component: Estoque
    },
    
  ]
})




export default router
